import React from 'react';

// components
import MessageAlert from './MessageAlert';

// redux
import { useDispatch, useSelector } from 'react-redux';
import { hideMessage } from '../../redux/modules/message';
import { RootState } from '../../index';


function MessageAlertContainer() {
  const dispatch = useDispatch();

  // state
  const state = useSelector((state: RootState) => state.message);

  // methods
  const methods = {
    close() {
      dispatch(hideMessage());
    }
  }

  return <MessageAlert state={state} methods={methods} />;
}

export default MessageAlertContainer;